.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0,0,0,0.2);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 20px;
}

.popup-inner {
  position: relative;
  padding: 32px;
  width: 100%;
  max-width: 640px;
  background-color: #FFF;
  border-radius: 20px;
}

.popup-inner .close-btn {
  position: absolute;
  top: 16px;
  right: 16px;
  border-radius: 5px;
  background-color: #682D43;
  color: white;
  padding: 0.2rem;
}



